<template>
  <div :class="isDarkMode ? 'theme-dark' : 'theme-light'" class="min-h-screen transition-all duration-500">
    <div class="container mx-auto px-4 sm:py-8 py-0 sm:px-0 relative">
      <!-- Theme Toggle Button -->
      <button @click="toggleTheme" class="theme-toggle-btn absolute top-2 right-4 p-3 rounded-full transition-all duration-300 z-50">
        <svg v-if="isDarkMode" xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
        </svg>
        <svg v-else xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
        </svg>
      </button>
      
      <h1 class="text-2xl sm:text-4xl font-bold theme-text mb-4 pt-4 text-center">Expense List</h1>
      
      <!-- Centered Two Column Layout -->
      <div class="flex justify-center items-center min-h-[80vh] p-0 sm:p-4">
        <div class="grid grid-cols-1 xl:grid-cols-2 gap-8 max-w-7xl w-full">
        
          <!-- Left Column: Controls and Summary -->
          <div class="space-y-6">
        <!-- Add/Edit Transaction Form -->
        <form @submit.prevent="addOrUpdateTransaction" class="glass-card p-6 rounded-lg shadow-lg">
          <h2 class="text-2xl font-semibold theme-text mb-6">{{ editingTransactionId ? 'Edit' : 'Add New' }} Expense</h2>
          
          <!-- Voice Recording Section -->
          
          <div class="grid grid-cols-1 gap-6 mb-6">
            <input type="text" v-model="form.name" placeholder="Name" class="form-input" required>
            
          </div>
          <div class="grid grid-cols-1 gap-6 mb-6">
            <input type="number" v-model.number="form.price_mmk" placeholder="Price (MMK)" class="form-input" @input="convertToDollars" required>

            <select v-model="form.type" class="form-input" required>
              <option value="income">Income</option>
              <option value="outcome">Outcome</option>
            </select>
          </div>
          <!-- Description field for edit only -->
          <div class="mb-6" v-if="editingTransactionId">
            <textarea v-model="form.description" placeholder="Description" class="form-input" rows="2"></textarea>
          </div>
          <!-- Date and Time fields, only for edit mode -->
          <div v-if="editingTransactionId" class="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
            <input type="date" v-model="form.date" class="form-input" required>
            <input type="time" v-model="form.time" class="form-input" required>
          </div>
          <button type="submit" class="btn-primary w-full">{{ editingTransactionId ? 'Update' : 'Add' }} Expense</button>
          <button v-if="editingTransactionId" @click="cancelEdit" type="button" class="btn-secondary w-full mt-3">Cancel Edit</button>
        </form>
          </div>

          <!-- Right Column: Transactions List -->
          <div class="space-y-6">
        <div class="glass-card p-6 rounded-lg shadow-lg">
          <div class="flex justify-between items-center mb-6">
            <h2 class="text-md sm:text-2xl font-semibold theme-text">Transactions</h2>
            <div class="flex items-center space-x-2">
              <select v-model="exportRange" class="form-input w-auto py-2 px-3 text-sm">
                <option value="daily">Daily</option>
                <option value="weekly">Weekly</option>
                <option value="monthly">Monthly</option>
                <option value="yearly">Yearly</option>
              </select>
              <button
          @click="exportToPDF" 
          :disabled="transactions.length === 0"
          class="btn-export flex items-center space-x-2"
          :class="{ 'opacity-50 cursor-not-allowed': transactions.length === 0 }"
            >
          <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 10v6m0 0l-3-3m3 3l3-3m2 8H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
          </svg>
          <span class="hidden sm:inline-flex">Export PDF</span>
            </button>
            </div>
          </div>
          
          <ul v-if="paginatedTransactions.length > 0" class="space-y-4 mb-8">
            <li v-for="transaction in paginatedTransactions" :key="transaction.id" 
            class="transaction-item glass-item p-5 rounded-lg shadow-md flex flex-col md:flex-row justify-between items-start md:items-center transition-all duration-300 hover:shadow-xl"
            :class="transaction.type === 'income' ? 'border-l-4 border-green-400' : 'border-l-4 border-red-400'">
              <div class="flex-grow mb-4 md:mb-0">
                <h3 class="text-xl font-semibold theme-text">{{ transaction.name }}</h3>
                <p class="text-sm theme-text-muted">{{ transaction.category }}</p>
                <p class="text-xs theme-text-muted hidden sm:inline-flex">{{ transaction.description }}</p>
              </div>
              <div class=" md:text-left sm:mb-4 md:mb-0 mb-8 md:mx-4">
                <p class="text-lg font-medium" :class="transaction.type === 'income' ? 'text-green-300' : 'text-red-300'">
                  {{ transaction.type === 'income' ? '+' : '-' }} {{ (transaction.price_mmk || 0).toFixed(2) }}
                MMK</p>

                <p class="text-sm theme-text-muted">${{ (transaction.price_usd || 0).toFixed(2) }}</p>
                <p class="text-xs theme-text-muted">{{ formatDate(transaction.date) }} at {{ formatTime(transaction.time) }}</p>
              </div>
              <div class="flex space-x-3 self-start md:self-center w-auto self-end">
                <button @click="editTransaction(transaction)" class="btn-icon text-blue-300 hover:text-blue-100">
                  <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z" /></svg>
                </button>
                <button @click="deleteTransaction(transaction.id)" class="btn-icon text-red-400 hover:text-red-200">
                  <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" /></svg>
                </button>
              </div>
            </li>
          </ul>
          
          <p v-else class="theme-text-muted text-center py-4">No list yet. Add one above!</p>
          <!-- Pagination Controls -->
          <div v-if="totalPages > 1" class="flex justify-center mt-8 space-x-2 space-y-2 sm:space-y-0 align-items-center ">
  <button class="btn-secondary px-1 py-1 w-full sm:w-auto mt-2 sm:mt-0" :disabled="currentPage === 1" @click="goToPage(currentPage - 1)">&laquo;</button>
  <button v-if="currentPage > 3" class="btn-secondary px-1 py-1 m-1 w-full sm:w-auto" @click="goToPage(1)">1</button>
  <span v-if="currentPage > 4" class="px-2 py-1 text-gray-400">...</span>
  <button v-for="page in pageNumbers" :key="page" class="btn-secondary px-1 py-1 m-1 w-full sm:w-auto" :class="{ 'bg-pink-600 text-white': page === currentPage }" @click="goToPage(page)">{{ page }}</button>
  <span v-if="currentPage < totalPages - 3 && totalPages > 5" class="px-2 py-1 text-gray-400">...</span>
  <button v-if="currentPage < totalPages - 2 && totalPages > 5" class="btn-secondary px-1 py-1 m-1 w-full sm:w-auto" @click="goToPage(totalPages)">{{ totalPages }}</button>
  <button class="btn-secondary px-1 py-1 w-full sm:w-auto mt-2 sm:mt-0" :disabled="currentPage === totalPages" @click="goToPage(currentPage + 1)">&raquo;</button>
</div>
        </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, reactive, computed } from 'vue';
import { gsap } from 'gsap';
import Swal from 'sweetalert2';
import RecordRTC from 'recordrtc';

export default {
  name: 'ExpenseTracker',
  setup() {    const transactions = ref([]);
    const editingTransactionId = ref(null);
    const exchangeRate = ref(4500);
    const usdAmount = ref(null);
    const mmkAmount = ref(null);
    const isDarkMode = ref(true);
    const exportRange = ref('weekly');
    const hasMore = ref(true);
    const loadingMore = ref(false);
    const currentPage = ref(1);
    const pageSize = ref(10);

    // Voice Recording State
    const isRecording = ref(false);
    const isProcessing = ref(false);
    const recordingTime = ref(0);
    const lastTranscription = ref('');
    const voiceError = ref('');
    const recorder = ref(null);
    const recordingTimer = ref(null);
    const mediaStream = ref(null);

    const form = reactive({
      id: null,
      name: '',
      type: 'outcome',
      price_usd: null,
      price_mmk: null,
      description: '',
      category: '',
      date: new Date().toISOString().slice(0, 10),
      time: new Date().toTimeString().slice(0, 5),
    });

    const API_URL = 'http://192.168.100.32:8888/expenseTracker/api/api.php';

    const totalIncome = computed(() => {
      return transactions.value
        .filter(transaction => transaction.type === 'income')
        .reduce((sum, transaction) => sum + (parseFloat(transaction.price_usd) || 0), 0);
    });

    const totalExpenses = computed(() => {
      return transactions.value
        .filter(transaction => transaction.type === 'outcome')
        .reduce((sum, transaction) => sum + (parseFloat(transaction.price_usd) || 0), 0);
    });

    const netBalance = computed(() => {
      return totalIncome.value - totalExpenses.value;
    });

    const paginatedTransactions = computed(() => {
      const start = (currentPage.value - 1) * pageSize.value;
      return transactions.value.slice(start, start + pageSize.value);
    });

    const totalPages = computed(() => Math.ceil(transactions.value.length / pageSize.value));

    const fetchTransactions = async () => {
      try {
        const response = await fetch(`${API_URL}?action=get_transactions`);
        if (!response.ok) throw new Error('Failed to fetch transactions');
        const data = await response.json();
        console.log('Raw backend response for get_transactions:', data);
        if (!Array.isArray(data)) {
          console.error('Expected array but got:', data);
          Swal.fire({
            icon: 'error',
            title: 'Backend Error',
            text: `Unexpected response from server. Check backend and error.log.`,
            confirmButtonColor: '#3b82f6'
          });
          transactions.value = [];
          return;
        }
        transactions.value = data.map(transaction => ({
          ...transaction,
          price_usd: parseFloat(transaction.price_usd) || 0,
          price_mmk: parseFloat(transaction.price_mmk) || 0
        }));
      } catch (error) {
        console.error('Error fetching transactions:', error);
        Swal.fire({
          icon: 'error',
          title: 'Fetch Error',
          text: `Could not fetch transactions. ${error.message}`,
          confirmButtonColor: '#3b82f6'
        });
      }
    };    const addOrUpdateTransaction = async () => {
      if (!form.name || form.price_usd === null || form.price_mmk === null || !form.date || !form.time) {
        Swal.fire({
          icon: 'warning',
          title: 'Missing Information',
          text: 'Please fill in all required fields.',
          confirmButtonColor: '#3b82f6'
        });
        return;
      }

      const transactionData = { ...form };
      let body = new FormData();

      if (editingTransactionId.value) {
        body.append('action', 'update_transaction');
        body.append('id', editingTransactionId.value);
      } else {
        body.append('action', 'add_transaction');
      }
      
      for (const key in transactionData) {
        if (key !== 'id' || !editingTransactionId.value) {
             body.append(key, transactionData[key]);
        }
      }

      try {
        const response = await fetch(API_URL, {
          method: 'POST',
          body: body,
        });
        if (!response.ok) {
            const errorText = await response.text();
            throw new Error(`Failed to save transaction: ${errorText}`);
        }
        await fetchTransactions();
        resetForm();
        animateFormSubmission();      } catch (error) {
        console.error("Error saving transaction:", error);
        Swal.fire({
          icon: 'error',
          title: 'Transaction Error',
          text: `Error: ${error.message}`,
          confirmButtonColor: '#3b82f6'
        });
      }
    };

    const editTransaction = (transaction) => {
      editingTransactionId.value = transaction.id;
      form.id = transaction.id;
      form.name = transaction.name;
      form.type = transaction.type;
      form.price_usd = parseFloat(transaction.price_usd);
      form.price_mmk = parseFloat(transaction.price_mmk);
      form.description = transaction.description;
      form.category = transaction.category;
      form.date = transaction.date;
      form.time = transaction.time;
      window.scrollTo({ top: 0, behavior: 'smooth' });
    };    const deleteTransaction = async (id) => {
      const result = await Swal.fire({
        title: 'Delete Transaction?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#ef4444',
        cancelButtonColor: '#6b7280',
        confirmButtonText: 'Yes, delete it!',
        cancelButtonText: 'Cancel'
      });

      if (!result.isConfirmed) return;

      try {
        let body = new FormData();
        body.append('action', 'delete_transaction');
        body.append('id', id);

        const response = await fetch(API_URL, {
          method: 'POST',
          body: body,
        });
        if (!response.ok) throw new Error('Failed to delete transaction');
        await fetchTransactions();
        animateTransactionDeletion();
        
        Swal.fire({
          title: 'Deleted!',
          text: 'Your transaction has been deleted.',
          icon: 'success',
          confirmButtonColor: '#3b82f6'
        });
      } catch (error) {
        console.error("Error deleting transaction:", error);
        Swal.fire({
          icon: 'error',
          title: 'Delete Failed',
          text: 'Failed to delete transaction. Please try again.',
          confirmButtonColor: '#3b82f6'
        });
      }
    };

    const resetForm = () => {
      editingTransactionId.value = null;
      form.id = null;
      form.name = '';
      form.type = 'outcome';
      form.price_usd = null;
      form.price_mmk = null;
      form.description = '';
      form.category = '';
      form.date = new Date().toISOString().slice(0, 10);
      form.time = new Date().toTimeString().slice(0, 5);
    };

    const cancelEdit = () => {
        resetForm();
    };

    const convertToKyats = () => {
      if (form.price_usd !== null && form.price_usd !== '') {
        form.price_mmk = parseFloat(form.price_usd) * exchangeRate.value;
      } else if (form.price_usd === '') {
        form.price_mmk = null;
      }
    };

    const convertToDollars = () => {
      if (form.price_mmk !== null && form.price_mmk !== '') {
        form.price_usd = parseFloat(form.price_mmk) / exchangeRate.value;
      } else if (form.price_mmk === '') {
        form.price_usd = null;
      }
    };

    const convertCurrency = () => {
        if (usdAmount.value !== null && usdAmount.value !== '') {
            mmkAmount.value = parseFloat(usdAmount.value) * exchangeRate.value;
        } else {
            mmkAmount.value = null;
        }
    };

    const toggleTheme = () => {
      isDarkMode.value = !isDarkMode.value;
      localStorage.setItem('expense-tracker-theme', isDarkMode.value ? 'dark' : 'light');
    };

    const animateFormSubmission = () => {
      gsap.fromTo('.transaction-item:first-child', { opacity: 0, y: -20 }, { opacity: 1, y: 0, duration: 0.5 });
    };

    const animateTransactionDeletion = () => {
      console.log("Transaction deleted animation trigger");
    };

    const formatDate = (dateString) => {
      const options = { year: 'numeric', month: 'long', day: 'numeric' };
      return new Date(dateString).toLocaleDateString(undefined, options);
    };    const formatTime = (timeString) => {
        if (!timeString) return '';
        const [hours, minutes] = timeString.split(':');
        const date = new Date();
        date.setHours(parseInt(hours), parseInt(minutes));
        return date.toLocaleTimeString(undefined, { hour: '2-digit', minute: '2-digit', hour12: true });
    };

    // Voice Recording Functions
    const toggleRecording = async () => {
      if (isRecording.value) {
        stopRecording();
      } else {
        await startRecording();
      }
    };

    const startRecording = async () => {
      try {
        voiceError.value = '';
        
        // Request microphone access
        const stream = await navigator.mediaDevices.getUserMedia({ 
          audio: {
            echoCancellation: true,
            noiseSuppression: true,
            sampleRate: 44100
          } 
        });
        
        mediaStream.value = stream;
        
        // Create recorder
        recorder.value = new RecordRTC(stream, {
          type: 'audio',
          mimeType: 'audio/wav',
          recorderType: RecordRTC.StereoAudioRecorder,
          numberOfAudioChannels: 1,
          desiredSampRate: 16000,
          bufferSize: 4096
        });
        
        recorder.value.startRecording();
        isRecording.value = true;
        recordingTime.value = 0;
        
        // Start timer
        recordingTimer.value = setInterval(() => {
          recordingTime.value++;
          if (recordingTime.value >= 60) { // Auto-stop after 60 seconds
            stopRecording();
          }
        }, 1000);
        
      } catch (error) {
        voiceError.value = 'Microphone access denied. Please enable microphone permissions.';
        console.error('Error starting recording:', error);
      }
    };

    const stopRecording = () => {
      if (!recorder.value || !isRecording.value) return;
      
      isRecording.value = false;
      isProcessing.value = true;
      
      // Clear timer
      if (recordingTimer.value) {
        clearInterval(recordingTimer.value);
        recordingTimer.value = null;
      }
      
      recorder.value.stopRecording(() => {
        const blob = recorder.value.getBlob();
        
        // Stop media stream
        if (mediaStream.value) {
          mediaStream.value.getTracks().forEach(track => track.stop());
        }
        
        // Process the audio blob
        processAudioBlob(blob);
      });
    };    const processAudioBlob = async (blob) => {
      try {
        // For now, we'll use the Web Speech API as fallback
        // In a real implementation, you would send this to a Whisper API
        
        // Convert blob to audio URL for future Whisper.cpp integration
        // const audioUrl = URL.createObjectURL(blob);
        // TODO: Send blob to Whisper.cpp API when implemented
        console.log('Audio blob ready for processing:', blob); // For debugging and future Whisper integration
        
        // Simulate Whisper processing with Web Speech API
        await processWithWebSpeech();
        
      } catch (error) {
        voiceError.value = 'Error processing audio. Please try again.';
        console.error('Error processing audio:', error);
        isProcessing.value = false;
      }
    };

    const processWithWebSpeech = () => {
      return new Promise((resolve, reject) => {
        if (!('webkitSpeechRecognition' in window) && !('SpeechRecognition' in window)) {
          // Fallback - simulate transcription for demo
          setTimeout(() => {
            lastTranscription.value = "Coffee expense 5 dollars outcome daily coffee purchase";
            isProcessing.value = false;
            resolve();
          }, 2000);
          return;
        }

        const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
        const recognition = new SpeechRecognition();
        
        recognition.continuous = false;
        recognition.interimResults = false;
        recognition.lang = 'en-US';
        
        recognition.onresult = (event) => {
          const transcript = event.results[0][0].transcript;
          lastTranscription.value = transcript;
          isProcessing.value = false;
          resolve();
        };
        
        recognition.onerror = (event) => {
          voiceError.value = 'Speech recognition error. Please try again.';
          isProcessing.value = false;
          reject(event.error);
        };
        
        recognition.onend = () => {
          if (isProcessing.value) {
            isProcessing.value = false;
            voiceError.value = 'No speech detected. Please try again.';
          }
        };
        
        // Start a new recording for speech recognition
        navigator.mediaDevices.getUserMedia({ audio: true })
          .then(stream => {
            recognition.start();
            setTimeout(() => {
              stream.getTracks().forEach(track => track.stop());
            }, 100);
          })
          .catch(error => {
            voiceError.value = 'Could not access microphone for speech recognition.';
            isProcessing.value = false;
            reject(error);
          });
      });
    };

    const applyTranscription = () => {
      if (!lastTranscription.value) return;
      
      try {
        const text = lastTranscription.value.toLowerCase();
        
        // Parse the transcription for transaction data
        parseTranscriptionToForm(text);
        
        Swal.fire({
          icon: 'success',
          title: 'Voice Input Applied',
          text: 'Transaction details have been filled from your voice input.',
          confirmButtonColor: '#3b82f6'
        });
        
      } catch (error) {
        Swal.fire({
          icon: 'warning',
          title: 'Parsing Error',
          text: 'Could not parse all details from voice input. Please check and complete the form.',
          confirmButtonColor: '#3b82f6'
        });
      }
    };

    const parseTranscriptionToForm = (text) => {
      // Simple parsing logic - can be enhanced with NLP
      
      // Extract amount (look for numbers followed by dollar/USD)
      const amountMatch = text.match(/(\d+(?:\.\d{2})?)\s*(?:dollar|usd|bucks?)/i);
      if (amountMatch) {
        form.price_usd = parseFloat(amountMatch[1]);
        form.price_mmk = form.price_usd * exchangeRate.value;
      }
      
      // Extract transaction type
      if (text.includes('income') || text.includes('salary') || text.includes('earning')) {
        form.type = 'income';
      } else if (text.includes('expense') || text.includes('spent') || text.includes('bought') || text.includes('purchase')) {
        form.type = 'outcome';
      }
      
      // Extract name/description (basic keyword matching)
      const keywords = ['coffee', 'lunch', 'dinner', 'gas', 'groceries', 'shopping', 'taxi', 'uber', 'salary', 'bonus'];
      for (const keyword of keywords) {
        if (text.includes(keyword)) {
          form.name = keyword.charAt(0).toUpperCase() + keyword.slice(1);
          form.category = getCategoryFromKeyword(keyword);
          break;
        }
      }
      
      // Set description
      form.description = lastTranscription.value;
      
      // Set current date and time if not already set
      if (!form.date) {
        form.date = new Date().toISOString().slice(0, 10);
      }
      if (!form.time) {
        form.time = new Date().toTimeString().slice(0, 5);
      }
    };

    const getCategoryFromKeyword = (keyword) => {
      const categoryMap = {
        'coffee': 'Food & Drink',
        'lunch': 'Food & Drink',
        'dinner': 'Food & Drink',
        'groceries': 'Food & Drink',
        'gas': 'Transportation',
        'taxi': 'Transportation',
        'uber': 'Transportation',
        'shopping': 'Shopping',
        'salary': 'Income',
        'bonus': 'Income'
      };
      return categoryMap[keyword] || 'Other';
    };

    const exportToPDF = async () => {
      if (transactions.value.length === 0) {
        Swal.fire({
          icon: 'info',
          title: 'No Data',
          text: 'No transactions to export!',
          confirmButtonColor: '#3b82f6'
        });
        return;
      }

      const exportButton = document.querySelector('.btn-export');
      const originalText = exportButton.innerHTML;

      try {
        exportButton.innerHTML = '<span>Generating PDF...</span>';
        exportButton.disabled = true;

        // Calculate date range based on exportRange
        let startDate, endDate;
        const today = new Date();
        if (exportRange.value === 'daily') {
          startDate = new Date(today);
          endDate = new Date(today);
        } else if (exportRange.value === 'weekly') {
          const day = today.getDay();
          startDate = new Date(today);
          startDate.setDate(today.getDate() - day + (day === 0 ? -6 : 1)); // Monday
          endDate = new Date(startDate);
          endDate.setDate(startDate.getDate() + 6); // Sunday
        } else if (exportRange.value === 'monthly') {
          startDate = new Date(today.getFullYear(), today.getMonth(), 1);
          endDate = new Date(today.getFullYear(), today.getMonth() + 1, 0);
        } else if (exportRange.value === 'yearly') {
          startDate = new Date(today.getFullYear(), 0, 1);
          endDate = new Date(today.getFullYear(), 11, 31);
        }
        // Format dates as yyyy-mm-dd
        const format = d => d.toISOString().slice(0, 10);
        const params = exportRange.value === 'daily'
          ? `?export_type=daily`
          : `?range=${exportRange.value}&start=${format(startDate)}&end=${format(endDate)}`;

        const response = await fetch(`http://192.168.100.32:8888/expenseTracker/api/export_pdf.php${params}`, {
          method: 'GET',
          headers: {
            'Accept': 'application/pdf',
          },
        });

        if (!response.ok) {
          // Try to parse JSON error from backend
          let errorMsg = `HTTP error! status: ${response.status}`;
          try {
            const text = await response.text();
            const json = JSON.parse(text);
            if (json && json.error) {
              errorMsg = json.error;
            }
          } catch (e) { /* intentionally empty: fallback to default error message */ }
          Swal.fire({
            icon: 'info',
            title: 'No Data',
            text: errorMsg,
            confirmButtonColor: '#3b82f6'
          });
          return;
        }

        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `expense-tracker-${exportRange.value}-${format(startDate)}-to-${format(endDate)}.pdf`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(url);
        Swal.fire({
          icon: 'success',
          title: 'Export Successful',
          text: 'PDF exported successfully!',
          confirmButtonColor: '#3b82f6'
        });
      } catch (error) {
        console.error('Error generating PDF:', error);
        Swal.fire({
          icon: 'error',
          title: 'Export Failed',
          text: 'Error generating PDF. Please try again.',
          confirmButtonColor: '#3b82f6'
        });
      } finally {
        exportButton.innerHTML = originalText;
        exportButton.disabled = false;
      }
    };

    const pageNumbers = computed(() => {
      const pages = [];
      if (totalPages.value <= 5) {
        for (let i = 1; i <= totalPages.value; i++) pages.push(i);
      } else if (currentPage.value <= 3) {
        for (let i = currentPage.value; i <= Math.min(currentPage.value + 2, totalPages.value - 1); i++) pages.push(i);
      } else if (currentPage.value >= totalPages.value - 2) {
        for (let i = totalPages.value - 3; i < totalPages.value; i++) if (i > 1) pages.push(i);
      } else {
        pages.push(currentPage.value - 1, currentPage.value, currentPage.value + 1);
      }
      return pages.filter((p, i, arr) => arr.indexOf(p) === i && p > 1 && p < totalPages.value);
    });

    function goToPage(page) {
      if (page < 1 || page > totalPages.value) return;
      currentPage.value = page;
    }

    onMounted(() => {
      const savedTheme = localStorage.getItem('expense-tracker-theme');
      if (savedTheme) {
        isDarkMode.value = savedTheme === 'dark';
      }
      
      fetchTransactions();
      gsap.from('.glassmorphism', { opacity: 0, scale: 0.9, duration: 0.8, ease: 'power2.out' });
      gsap.from('h1', { opacity: 0, y: -30, duration: 0.7, delay: 0.3 });
    });    return {
      transactions,
      form,
      editingTransactionId,
      addOrUpdateTransaction,
      editTransaction,
      deleteTransaction,
      resetForm,
      cancelEdit,
      convertToKyats,
      convertToDollars,
      formatDate,
      formatTime,
      exchangeRate,
      usdAmount,
      mmkAmount,
      convertCurrency,
      isDarkMode,
      toggleTheme,
      exportRange,
      exportToPDF,
      totalIncome,
      totalExpenses,
      netBalance,
      hasMore,
      loadingMore,
      fetchTransactions,
      // Voice Recording
      isRecording,
      isProcessing,
      recordingTime,
      lastTranscription,
      voiceError,
      toggleRecording,
      applyTranscription,
      paginatedTransactions,
      currentPage,
      totalPages,
      goToPage,
      pageSize,
      pageNumbers,
    };
  },
};
</script>

